import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
// import tileData from 'tileData';
import deal1 from '../images/deal1.png'
import deal2 from '../images/deal2.png'
const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
}));


export default function SingleLineGridList() {
  const classes = useStyles();
  const tileData = [
    {
      img: deal1,
      title: 'Image',
      price: 'author',
    }, {
      img: deal2,
      title: 'Image',
      price: 'author',
    },
    {
      img:'https://cloud.24hours.pk/images/deals/full/54801504780102.jpg',
      title: 'Image',
      price: 'author',
    }, {
      img: 'http://www.foodjunction.com.pk/img/deals-update/lg/family-deal-(chinese).jpg',
      title: 'Image',
      price: 'author',
    }
  ]

  return (
    <div className={classes.root}>
      <h2 className="font-weight-bold my-5 text-center">Special Offers</h2>
      <GridList className={classes.gridList} cols={2.5}>
        {tileData.map(tile => (
          <GridListTile key={tile.img}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              actionIcon={
                <IconButton aria-label={`star ${tile.title}`}>
                  <StarBorderIcon className={classes.title} />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
  );
}
